import { Scene } from '../Core/Scene';
export class Tracer {
    scene: Scene;
    divHost: HTMLElement;
    div: HTMLElement
    ptsGeoTracing: number[] = undefined;
    toMap: (x: number, y: number) => number[];
    toScreen: (x: number, y: number) => number[];
    destroyApp: () => void;
    constructor(divHost: HTMLElement, toMap: (x: number, y: number) => number[], toScreen: (x: number, y: number) => number[], destroy: () => void ) {
        this.divHost = divHost;
        this.toScreen = toScreen;
        this.toMap = toMap;
        this.destroyApp = destroy;
        this.ptsGeoTracing = [];
        this.initHTML();
    }
    public initHTML() {
        const div = document.createElement('div');
        this.div = div;
        div.style.left = '200px';
        div.style.width = '100px';
        div.style.height = '200px';
        div.style.padding = '10px';
        div.style.position = 'absolute';
        div.style.background = '#000';
        this.divHost.appendChild(div);


        let divBtn = document.createElement('div');
        divBtn.style.marginBottom = '5px';
        divBtn.style.width = '20px';
        divBtn.style.height = '20px';
        divBtn.textContent = 'Tracer';
        divBtn.style.color = '#fff';
        div.appendChild(divBtn);

        divBtn = document.createElement('div');
        divBtn.style.marginBottom = '5px';
        divBtn.style.height = '20px';
        divBtn.textContent = 'X';
        divBtn.style.color = '#fff';
        div.appendChild(divBtn);
        divBtn.onclick = () => {
            this.ptsGeoTracing = [];
            this.destroy();
        }

        divBtn = document.createElement('div');
        divBtn.style.marginBottom = '5px';
        divBtn.style.height = '20px';
        divBtn.textContent = 'Reset';
        divBtn.style.color = '#fff';
        divBtn.onclick = () => {
            this.ptsGeoTracing = [];
        }
        div.appendChild(divBtn);


        divBtn = document.createElement('div');
        divBtn.style.marginBottom = '5px';
        divBtn.style.height = '20px';
        divBtn.textContent = 'Print';
        divBtn.style.color = '#fff';
        divBtn.onclick = () => {
            console.log(this.ptsGeoTracing);
        }
        div.appendChild(divBtn);
    }
    public click(x: number, y: number) {
        const mp = this.toMap(x, y);
        console.log(`Lon:${mp[0]}, Lat:${mp[1]}`);
        this.ptsGeoTracing.push(mp[0], mp[1]);
    }
    public startCapture() {
        this.ptsGeoTracing = [];
    }
    public endCapture() {
        this.ptsGeoTracing = undefined;
    }
    public render(ctx: CanvasRenderingContext2D) {
        if (this.ptsGeoTracing !== undefined) {

            if (this.ptsGeoTracing.length > 3) {
                // console.log(this.ptsGeo.length)
                ctx.beginPath();
                let pos = this.toScreen(this.ptsGeoTracing[0], this.ptsGeoTracing[1])
                // console.log(pos)
                ctx.moveTo(pos[0], pos[1])
                for (let i = 2; i < this.ptsGeoTracing.length; i += 2) {
                    pos = this.toScreen(this.ptsGeoTracing[i], this.ptsGeoTracing[i + 1])
                    ctx.lineTo(pos[0], pos[1])
                }
                ctx.lineWidth = 2;
                ctx.strokeStyle = '#000000';
                ctx.stroke();
                // console.log(pos)


            }

            for (let i = 0; i < this.ptsGeoTracing.length; i += 2) {
                let pos = this.toScreen(this.ptsGeoTracing[i], this.ptsGeoTracing[i + 1])
                ctx.beginPath();
                ctx.arc(pos[0], pos[1], 10, 0, Math.PI * 2.);
                ctx.closePath();
                ctx.fillStyle = '#fff';
                ctx.fill();
            }
        }
    }
    public destroy = () => {
        this.div.parentElement.removeChild(this.div);
        this.destroyApp();
    }
}