export class WaterHyacinth {
    public divHost: HTMLElement
    public core: Core;
    public renderer: Renderer;
    public ui: UI;
    public project: Project;
    public param: any;
    public addon: Addon;
    constructor(view: any) {

        if (Boolean(PARAM['demo']) === true) {
            this.init(view);
        }
    }
    private init(view: any) {
        this.divHost = view;
        this.divHost.id = 'mapboxgl-container'
        this.param = PARAM;

        this.renderer = new Renderer(this);
        this.core = new Core(this);
        this.project = new Project(this, siteData);
        this.addon = new Addon(this);
        this.ui = new UI(this);
        this.renderer.init();
    }
    public destroy() {
        // this.renderer.destroy();
        if (this.divHost){
            while (this.divHost.lastElementChild) {
                this.divHost.removeChild(this.divHost.lastElementChild);
            }
        }

    }
}
