import { Core } from '.';
import { Site } from '../Project/Site';

export class ActiveCommand {
    public core: Core;
    public m = [0, 0, 0, 0];
    constructor(core: Core) {
        this.core = core;
    }
    public mouseDown(x: number, y: number) {

    }
    public mouseUp(x: number, y: number) {
        
    }
    public mouseDrag(x: number, y: number) {
        
    }
    public mouseMove(x: number, y: number, lon: number, lat: number) {
        this.m[0] = x;
        this.m[1] = y;
        this.m[2] = lon;
        this.m[3] = lat;
        this.core.common.checkHover(x, y);
    }
    public mouseClick(x: number, y: number) {
        if (this.core.app.addon.click(x, y)) {
            return;
        }

        if(this.core.scene.capture) {
            this.core.scene.capture.click(x,y);
            return;
        }

        const sites: Site[] = this.core.common.getCloseSite(x, y);
        if (sites.length === 1) {
            this.core.scene.capture = sites[0];
        } else if (sites.length >1) {
            // when you click overlapping points we want to zoom in 
            this.core.common.zoom += this.core.common.zoom;
            this.core.common.center(sites[0].properties.lon, sites[0].properties.lat);
        }
        // const mapProjected = this.toMap(x, y);
        // // this.pts.push(mapProjected);
        // if (this.ptsGeoTracing !== undefined) {
        //     this.ptsGeoTracing.push(mapProjected[0], mapProjected[1])
        // }

        // if (this.app.siteFocused === undefined) {
        //     const selSite = this.app.core.getCloseSite(x, y);

        //     if (selSite) {
        //         selSite.focus();
        //         this.app.siteFocused = selSite;
        //     }

        // } else {
        //     this.app.siteFocused.click(x, y);
        // }
    }
    public zoomOut(){
        this.core.app.renderer.zoomOut();
    }
}