
import { remap, Vector3, isInsideOfVecs, distanceTo } from '../../../lib';
// import { MathUtility, NVector3 } from '../../../../../lib/NJSCore';

import { Site } from '..'

export class Boundary {
    site: Site;
    lon: number;
    lat: number;

    vsBoundary: Vector3[] = [];

    pMin: Vector3 = new Vector3(180, 90, 0);
    pMax: Vector3 = new Vector3(-180, -90, 0);
    pCenter: Vector3 = new Vector3(0, 0, 0);

    ctxTemp: CanvasRenderingContext2D;

    constructor(site: Site, boundary: number[]) {
        this.site = site;

        for (let i = 0; i < boundary.length; i += 2) {

            this.pCenter.x += boundary[i];
            this.pCenter.y += boundary[i + 1];

            if (boundary[i] < this.pMin.x) { this.pMin.x = boundary[i]; }
            if (boundary[i + 1] < this.pMin.y) { this.pMin.y = boundary[i + 1]; }

            if (boundary[i] > this.pMax.x) { this.pMax.x = boundary[i]; }
            if (boundary[i + 1] > this.pMax.y) { this.pMax.y = boundary[i + 1]; }

            this.vsBoundary.push(new Vector3(boundary[i], boundary[i + 1], 0))
        }

        this.pCenter.x /= boundary.length * 0.2
        this.pCenter.y /= boundary.length * 0.2

        const img = new Image(); // Create new img element
        img.src = process.env.PUBLIC_URL + '/static/data/Watabot/freshPond.jpg';
        img.crossOrigin = "Anonymous";
        img.onload = () => {
            const xNum = 50;
            const yNum = 50;

            const width = img.width;
            const height = img.height;
            const zScale = 5.5;

            const xInterval = width / (xNum - 1.0);
            const yInterval = height / (yNum - 1.0);

            const canvas = document.createElement('canvas');
            canvas.width = width;
            canvas.height = height;
            // this.hostDiv.append(canvas);
            const ctx = canvas.getContext('2d', {willReadFrequently: true} );
    
            ctx.drawImage(img, 0, 0);
            this.ctxTemp = ctx;

            // console.log('done loading image');
            // const vertices = [];
            // for (let y = 0; y < yNum; ++y) {
            //     for (let x = 0; x < xNum; ++x) {
            //         let col = ctx.getImageData(x * xInterval, y * yInterval, 1, 1).data;
            //         let zVal = RGBtoGray(col[0] / 255, col[1] / 255, col[2] / 255);
            //         let z = -zVal * zScale;
            //         vertices.push(x - (xNum * 0.5), y - (yNum * 0.5), z);
            //     }
            // }
        }

    }
    getRandomSpot() {
        let x = remap(Math.random(), 0, 1, this.pMin.x, this.pMax.x);
        let y = remap(Math.random(), 0, 1, this.pMin.y, this.pMax.y);
        let vec = new Vector3(x, y);
        let i = 0
        while (!isInsideOfVecs(vec, this.vsBoundary)) {
            vec.x = remap(Math.random(), 0, 1, this.pMin.x, this.pMax.x);
            vec.y = remap(Math.random(), 0, 1, this.pMin.y, this.pMax.y);
            if (i++ > 1000) {
                break
            }
        }
        // console.trace();
        return vec;
    }
    render(ctx: CanvasRenderingContext2D) {
        if (this.vsBoundary.length < 1) {

            let col = '#ff0000'
            if (this.site.properties.probability < 0.3) {
                col = '#00ff00'
            }

            ctx.fillStyle = col;
            ctx.beginPath();
            ctx.arc(this.site.vec.x, this.site.vec.y, 10, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fill();

        } else {
            let pos = this.site.project.app.core.common.toScreen(this.vsBoundary[0].x, this.vsBoundary[0].y)
            ctx.moveTo(pos[0], pos[1])
            for (let i = 1; i < this.vsBoundary.length; i += 1) {
                pos = this.site.project.app.core.common.toScreen(this.vsBoundary[i].x, this.vsBoundary[i].y)
                ctx.lineTo(pos[0], pos[1])
            }
            ctx.lineWidth = 2;
            ctx.strokeStyle = '#000000';
            ctx.stroke();
        }

        const res = 20
        const intervalX = (this.pMax.x - this.pMin.x) / (res - 1);
        const intervalY = (this.pMax.y - this.pMin.y) / (res - 1);

        const interXImage = this.ctxTemp.canvas.width / (res - 1);
        const interYImage = this.ctxTemp.canvas.height / (res - 1);

        let minDis = Number.MAX_SAFE_INTEGER;
        let id = [0,0];
        for (let i = 0; i < res; ++i) {
            const x = this.pMin.x + intervalX * i
            for (let j = 0; j < res; ++j) {
                const y = this.pMin.y + intervalY * j

                if (isInsideOfVecs(new Vector3(x, y), this.vsBoundary)) {
                    const sp = this.site.project.app.core.common.toScreen(x, y)
                    // console.log(sp);
    
                    let col = this.ctxTemp.getImageData(i * interXImage, j * interYImage, 1, 1).data;
                    // console.log(col);
                    ctx.fillStyle = `rgba(${col[0]}, ${col[1]}, ${col[2]}, 1)`;
                    ctx.beginPath();
                    ctx.arc(sp[0], sp[1], 10, 0, Math.PI * 2.);
                    ctx.fill();

                    let dis = distanceTo(this.site.project.app.core.activeCommand.m[2], this.site.project.app.core.activeCommand.m[3], 0, x, y, 0);
                    if (dis < minDis) {
                        minDis = dis;
                        id = [i, j]
                    }
                }

            }
        }

        // console.log(id);
        let xHover = this.pMin.x + intervalX * id[0];
        let yHover = this.pMin.y + intervalY * id[1];
        const sp = this.site.project.app.core.common.toScreen(xHover, yHover)
        ctx.strokeStyle = `rgba(255, 255,255, 1)`;
        ctx.beginPath();
        ctx.arc(sp[0], sp[1], 10, 0, Math.PI * 2.);
        ctx.stroke();

    }
}