import { WaterHyacinth  } from '..';
import { Site } from './Site';

export class Project {
    public rawSiteData: any
    public app: WaterHyacinth;
    public sites: Site[] = [];
    constructor(app: WaterHyacinth, siteData: any) {
        this.app = app;
        this.initSite(siteData)
    }
    public initSite(siteData: any) {
        this.rawSiteData = siteData;
        for (let i = 0; i < siteData.length; ++i) {
            const siteObj = new Site(this, siteData[i]);
            this.sites.push(siteObj);
        }
    }
}