    public RenderCircle(ctx: CanvasRenderingContext2D) {
        // console.log(this.p)

        const vec = this.BoidContollerWander0.site.project.app.core.common.toScreen(this.p.x, this.p.y);
        // console.log(vec)

        this.t += 0.05
        this.timerTrajectory += 1;
        if (this.timerTrajectory % 5 === 0) {
            if (this.trajectory.length > 30) {
                this.trajectory.shift();
            }
            this.trajectory.push(new Vector3(this.p.x, this.p.y));

            this.timerTrajectory = 0;
        }
        for (let i = 0; i < this.trajectory.length; ++i) {
            ctx.fillStyle = 'rgba(100, 100, 100 ,0.1)';
            ctx.beginPath();
            ctx.arc(this.trajectory[i].x, this.trajectory[i].y, (i) * 0.2, 0, 3.14 * 2);
            ctx.closePath();
            ctx.fill();
        }

        ctx.strokeStyle = '#000000';
        ctx.lineWidth = 0.5;
        // ctx.beginPath();
        // ctx.moveTo(this.targets[0].x , this.targets[0].y);
        // for (let i = 1 ; i < this.targets.length; ++i) {
        //   ctx.lineTo(this.targets[i].x , this.targets[i].y);
        // }
        // ctx.closePath();
        // ctx.stroke();

        // const target = this.BoidContollerWander0.site.project.app.core.common.toScreen(this.target.x, this.target.y);

        // ctx.beginPath();
        // ctx.moveTo(vec[0], vec[1]);
        // ctx.lineTo(target[0], target[1]);
        // ctx.stroke();
  
        // ctx.fillStyle = '#000000';
        // ctx.beginPath();
        // ctx.arc(this.target.x, this.target.y, 2.5, 0, 3.14 * 2);
        // ctx.closePath();
        // ctx.fill();



        ctx.fillStyle = '#000000';
        ctx.beginPath();
        ctx.arc(this.target.x, this.target.y, 2.5, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();


        ctx.fillStyle = '#fff';
        ctx.beginPath();
        ctx.arc(vec[0], vec[1], 2.5, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();
        ctx.beginPath();
        ctx.arc(vec[0], vec[1], 10 + Math.sin(this.t) * 5, 0, 3.14 * 2);
        ctx.closePath();
        // ctx.fill();
        ctx.stroke();

        // animation
        ctx.lineWidth = 0.5;
        ctx.strokeStyle = `rgba(${255}, ${255}, ${0}, ${1 - this.scaleBounding * 0.07})`;
        ctx.fillStyle = `rgba(${255}, ${255}, ${0}, ${1 - this.scaleBounding * 0.07})`;
        ctx.beginPath();
        ctx.arc(vec[0], vec[1], this.scaleBounding, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();
        ctx.stroke();
        this.scaleBounding += 0.2;
        if (1 - this.scaleBounding * 0.07 < 0) {
            this.scaleBounding = 0;
        }


    }
