import { WaterHyacinth } from '..';
export class UI {
    solution: WaterHyacinth;

    constructor(solution: WaterHyacinth) {
        this.solution = solution;

        const style = {
            menuMain: {
                color: '#fff',
                background: 'rgba(173, 216, 230, 0.2)',
                backdropFilter: 'blur(10px)',
            }
        }

        let container = document.createElement('div');
        container.style.position = 'absolute';
        container.style.width = '200px';
        container.style.height = '100%';
        container.style.padding = '10px';

        container.style.color = style.menuMain.color;
        container.style.background = style.menuMain.background;
        container.style.backdropFilter = style.menuMain.backdropFilter

        this.solution.divHost.append(container);


        const divTitle = getDivBtn(container, 'Map');
        const zoomTo = getDivBtn(container, 'Zoom out');
        zoomTo.onclick = () => {
            this.solution.core.activeCommand.zoomOut()
        }

        const separate = getDivBtn(container, '----------');
        const Addon = getDivBtn(container, 'Addon');
        const Tracer = getDivBtn(container, 'BoundaryTracer');
        Tracer.onclick = () => {
            this.solution.addon.appendTracer();
        };
        
    }
}


const getDivBtn = (host: HTMLDivElement, title: string) => {
    const div = document.createElement('div')
    host.appendChild(div);
    div.style.marginTop = '5px';
    div.textContent = title;
    return div;
}