
import { WaterHyacinth } from '..';
import { ActiveCommand } from './ActiveCommand'
import { Common } from './Common'
import { Scene } from './Scene'
// import { ActiveCommand } from '../Core/ActiveCommand'


export class Core {
    public app: WaterHyacinth;
    public activeCommand: ActiveCommand;
    public common: Common;
    public scene: Scene;

    constructor(app: WaterHyacinth) {
        this.app = app;
        this.activeCommand = new ActiveCommand(this);
        this.common = new Common(this);
        this.scene = new Scene(this);
    }

}