
import { WaterHyacinth } from '../';

import { Tracer } from './Tracer';

export class Addon {
    public app: WaterHyacinth;
    public tracer: Tracer;
    constructor(app: WaterHyacinth) {
        this.app = app;

        // console.log(this.app.param['dev'])
        if (this.app.param['dev']) {
            this.initDev();
        }
    }

    public initDev() {
        this.appendTracer();
    }
    public appendTracer() {
        this.tracer = new Tracer(
            this.app.divHost, this.app.core.common.toMap, this.app.core.common.toScreen,
            () => { this.tracer = undefined; }
            );
    }
    public click(x: number, y: number) {
        if (this.tracer) {
            this.tracer.click(x, y);
            return true;
        }
        return false;
    }
    public render(ctx: CanvasRenderingContext2D) {
        if (this.tracer) {
            this.tracer.render(ctx);
        }
    }
}