import { Project } from '..';
import { distanceTo, Vector3, isInsideOfVecs, remap, renderPoint } from '../../lib';
import { BoidContollerWander0 } from './Agent/BoidContollerWander0';
// import { MathUtility, NVector3 } from '../../../../../lib/NJSCore';

import { Boundary } from './Boundary';

class Properties {
    probability: number = 0.0;
    lon: number;
    lat: number;
    zoom: number;
    name: string;
}

export class Site {
    project: Project;

    properties: Properties;

    vec: Vector3 = new Vector3(0, 0, 0);
    r: number = 15;

    boundary: Boundary;
    // boundary: Vector3[] = [];

    pMin: Vector3 = new Vector3(180, 90, 0);
    pMax: Vector3 = new Vector3(-180, -90, 0);

    BoidContollerWander0: BoidContollerWander0;
    needAgent = false;

    isActivated = false;

    hover: boolean = false;

    constructor(solution: Project, properties) {
        this.project = solution;

        this.properties = new Properties();
        this.properties.lon = properties.loc[0];
        this.properties.lat = properties.loc[1];
        this.properties.zoom = properties.zoom;
        this.properties.name = properties.name;
        this.properties.probability = properties.probability;

        this.r = 15;

        this.boundary = new Boundary(this, properties.boundary);
    }
    public initAgent() {
        this.BoidContollerWander0 = new BoidContollerWander0(this)
        this.needAgent = true;
    }
    public focus() {
        this.project.app.core.common.center(this.properties.lon, this.properties.lat);
        this.project.app.core.common.zoom = this.properties.zoom;
    }
    public blur() {

    }
    public getLog() {
        return this.properties.name
    }
    public click(x: number, y: number) {
        // console.log(x, y);

        if (this.BoidContollerWander0 === undefined) {
            this.initAgent();
            this.BoidContollerWander0.AddAgent(this.vec.x, this.vec.y)
        }

        // const dis = distanceTo(x, y, 0, this.sx, this.sy, 0);
        // if (dis < this.r) {
        //     this.isActivate = true;
        //     this.focus();
        // this.initAgent();
        // } else {
        //     this.isActivate = false;
        // }


        // if(!this.isActivate) { return; }


        // console.log(dis, this.r)

        if (this.BoidContollerWander0 && this.needAgent) {
            // let pos = this.project.app.core.common.toMap(x, y)
            // console.log(pos)
            this.BoidContollerWander0.AddAgent(x, y)
        }

        // console.log(this.getRandomSpot())
    }
    public isHover(x: number, y: number) {
        if (distanceTo(x, y, 0, this.vec.x, this.vec.y, 0) < this.r) {
            this.hover = true;
        } else {
            this.hover = false;
        }
    }
    public renderDot(ctx: CanvasRenderingContext2D) {
        let pos = this.project.app.core.common.toScreen(this.properties.lon, this.properties.lat);
        this.vec.x = pos[0];
        this.vec.y = pos[1];

        if (this.project.app.core.common.zoom < this.properties.zoom - 2) {
            let col = '#ff0000'
            if (this.properties.probability < 0.3) {
                col = '#00ff00'
            }

            let t = 1;
            ctx.fillStyle = col;
            ctx.beginPath();
            ctx.arc(this.vec.x, this.vec.y, (Math.sin(t * 0.8) + 4) * 2, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fill();

            if (this.hover) {
                ctx.lineWidth = 3
                ctx.strokeStyle = '#ffffff';
                ctx.stroke();
            }

            if (this.hover) {
                ctx.beginPath();
                ctx.font = "12px Arial";
                ctx.fillText(this.properties.name, this.vec.x + 15, this.vec.y);
            }

        }


        if (this.project.app.core.common.zoom < this.properties.zoom - 2) {
            if (this.project.app.core.scene.capture === this) {
                this.project.app.core.scene.capture = undefined;
            }
        }
    }
    public render(ctx: CanvasRenderingContext2D) {
        // console.log(this.isActivated)
        if (!this.isActivated) { return; }
        // if (this.project.app.core.common.zoom < this.properties.zoom - 2) {
        //     let col = '#ff0000'
        //     if (this.properties.probability < 0.3) {
        //         col = '#00ff00'
        //     }

        //     let t = 1;
        //     ctx.fillStyle = col;
        //     ctx.beginPath();
        //     ctx.arc(this.vec.x, this.vec.y, (Math.sin(t * 0.8) + 4) * 2, 0, Math.PI * 2);
        //     ctx.closePath();
        //     ctx.fill();

        //     if (this.hover) {
        //         ctx.lineWidth = 3
        //         ctx.strokeStyle = '#ffffff';
        //         ctx.stroke();
        //     }

        //     if (this.project.app.core.scene.capture === this){
        //         this.project.app.core.scene.capture = undefined;
        //     }

        // }

        // console.log(this.project.app.core.common.getZoomLevel())

        // if(!this.isActivate) { return; }

        // if (this.needAgent === false) {
        //     this.initAgent();
        // }

        if (this.project.app.core.common.zoom >= this.properties.zoom - 2) {
           this.boundary.render(ctx);

        }
        // console.log(this.BoidContollerWander0)
        if (this.BoidContollerWander0) {
            this.BoidContollerWander0.Update();
            this.BoidContollerWander0.Render(ctx);
        }
    }
}