import { WaterHyacinth } from '../..';
import { MapboxGLWrapper } from '../../lib/MapboxGLWrapper';
import { getFalseColor } from 'njscore';
import { RendererCanvasDynamic } from '../../lib/Canvas';

export class Renderer {
    public app: WaterHyacinth;
    public map: MapboxGLWrapper;
    public canvasCustom: RendererCanvasDynamic;

    public colorArray1: number[][] = [];
    public colorArray2: number[][] = [];

    private originalZoom = 3;
    private originalCenter = [-98.292649, 39.043903];

    constructor(app: WaterHyacinth) {
        this.app = app;
        app.divHost.style.position = 'relative';
        this.map = new MapboxGLWrapper(app.divHost.id);
        this.map.mouseClick = (e) => this.mouseClick(e);
        // this.map.mouseDClick = this.mouseDClick;
        this.map.mouseDown = (e) => this.mouseDown(e);
        this.map.mouseUp = (e) => this.mouseUp(e);
        this.map.mouseMove = (e) => this.mouseMove(e);

        this.canvasCustom = new RendererCanvasDynamic(app.divHost)
        this.canvasCustom.render = this.render;

        this.colorArray1 = [];
        for (let i = 0; i < 255; ++i) {
            const result = getFalseColor(i / 255);
            this.colorArray1.push(result);
        }

        this.colorArray2 = [];
    }
    public init() {
        this.zoomOut();
    }
    public render = (ctx: CanvasRenderingContext2D) => {
        ctx.globalCompositeOperation = 'screen'; //'lighten';

        const xOff = ctx.canvas.width - 300;
        const yOff = ctx.canvas.height - 50;

        const w = 1;
        const h = 10;
        for (let i = 0; i < this.colorArray1.length; ++i) {
            ctx.beginPath();
            ctx.rect(i * w + xOff, yOff, w, h);
            ctx.closePath();
            ctx.fillStyle = `rgba(${this.colorArray1[i][0]}, ${this.colorArray1[i][1]}, ${this.colorArray1[i][2]}, 1)`;
            ctx.fill();
        }

        this.app.core.scene.render(ctx);
    }
    public zoomOut() {
        this.map.zoom = this.originalZoom;
        this.map.center(this.originalCenter[0], this.originalCenter[1])
    }
    private processEventData = (e: any): number[] => {
        const lon = e['lngLat']['lng'];
        const lat = e['lngLat']['lat'];
        const x = e['point']['x'];
        const y = e['point']['y'];
        return [x, y , lon, lat];
    }
    public keyDown(k: KeyboardEvent): void {
        // console.log('key pressed', k);
    }
    public mouseDown(e): void {
        const m = this.processEventData(e)
        this.app.core.activeCommand.mouseDown(m[0], m[1]);

        this.canvasCustom.forceRender();
    }
    public mouseUp(e): void {
        const m = this.processEventData(e)
        this.app.core.activeCommand.mouseUp(m[0], m[1]);
        // this.app.core.activeCommand.mouseUp(x, y);
    }
    public mouseClick(e): void {
        const m = this.processEventData(e)
        this.app.core.activeCommand.mouseClick(m[0], m[1]);
        // this.app.core.activeCommand.mouseClick(x, y);
    }
    public mouseMove(e): void {
        const m = this.processEventData(e)
        this.app.core.activeCommand.mouseMove(m[0], m[1], m[2], m[3]);
        // this.app.core.activeCommand.mouseMove(x, y);
    }
}
